import { months, weekdays } from '../config/constants';

//    ,   ,    number
export const formatDuration = (duration) => {
  const hours = parseInt(duration / (60 * 60), 10);
  const minutes = parseInt((duration / 60) % 60, 10);
  const seconds = duration % 60;
  const format = (value) => (value < 10 ? `0${value}` : `${value}`);

  return {
    hours: format(hours),
    minutes: format(minutes),
    seconds: format(seconds),
  };
};

//       ,    string  "2021-05-08T21:22:00Z"
export const formatDate = (date) => {
  const parsedDate = new Date(date);
  let day = parsedDate.getDate();
  const weekdayName = weekdays[parsedDate.getDay()];
  const monthName = months[parsedDate.getMonth() + 1]; //   months   1,   0
  const hour = String(parsedDate.getHours());
  const year = String(parsedDate.getFullYear());
  let minutes = parsedDate.getMinutes();

  if (minutes < 10) {
    minutes = `0${minutes}`;
  }

  if (day < 10) {
    day = `0${day}`;
  }

  return {
    year,
    day,
    weekdayName,
    monthName,
    hour,
    minutes,
  };
};

//    "2021-05-08T21:22:00Z"  'YYYY/MM/DD'
export const parseDate = (dateString) => {
  const date = new Date(dateString);
  const year = date.getFullYear();
  let month = date.getMonth() + 1;
  let day = date.getDate();
  if (day < 10) {
    day = `0${day}`;
  }
  if (month < 10) {
    month = `0${month}`;
  }
  return `${year}-${month}-${day}`;
};

//   ""     
//           
//   , 
export const formatWordCase = (remainSeats) => {
  //    1 -  
  //    2 3 4 -  A
  //     -  
  const lastDigit = remainSeats % 10;

  if (lastDigit === 1) {
    return '';
  }
  if (lastDigit === 2 || lastDigit === 3 || lastDigit === 4) {
    return 'a';
  }
  return '';
};

//     
//   
export const getCardType = (tags) => {
  if (tags) {
    return tags.map((tag, idx) => {
      if (tags.length === 1) {
        return `${tag.name}`;
      }
      if (idx !== tags.length - 1) {
        return `${tag.name} + `;
      }
      return `${tag.name.toLowerCase()}`;
    });
  }
  return undefined;
};

